import React, {useEffect} from 'react';
import { motion } from "framer-motion";
import Particles from 'react-particles-js';
import FadeIn from "react-lazyload-fadein";

function ChoosePara(status) {
  const lineHeight = 24; //Each text line adds 24px to height
  switch (status) {
    case 0:
      return <div className="text-container">
      <motion.h1 className="education-text-h"
      key="/education_text_h"
      initial={{ opacity: 0, x:0, y:20 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0 }}>
  	  	Education
      </motion.h1>
      <motion.p className="education-text-1"
      key="/education_text1"
      initial={{ opacity: 0, x:0, y:40 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.1 }}>
  	  	After several years of advanced study, I have attained two graduate and one undergraduate degree.
      </motion.p>
      <motion.p className="education-text-1"
      key="/education_text2"
      initial={{ opacity: 0, x:0, y: 60 + 2 * lineHeight}}
      animate={{ opacity: 1, x:0, y: 20 + 2 * lineHeight}}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.2 }}>
  	  	The degrees include a <span style={{color:'#ff9933', fontWeight:'bold'}}>Master of Business Administration</span> from the W.A. Franke College of Business at Northern Arizona University, and a <span style={{color:'#ff9933', fontWeight:'bold'}}>Master of Science in Engineering</span> and a <span style={{color:'#ff9933', fontWeight:'bold'}}>Bachelor of Science</span> from Chalmers University of Technology.
      </motion.p>
      <motion.p className="education-text-1"
      key="/education_text3"
      initial={{ opacity: 0, x:0, y: 80 + 6 * lineHeight }}
      animate={{ opacity: 1, x:0, y: 40 + 6 * lineHeight }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.3 }}>
  	  	My MBA had a concentration on Applied Management, while my MSc was in System, Control and Mechatronics, and my BSc was in Engineering Physics.
      </motion.p>
      </div>
    case 1:
      return <div className="text-container">
      <motion.h1 className="education-text-h"
      key="/chalmers_text_h"
      initial={{ opacity: 0, x:0, y:20 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0 }}>
  	  	Chalmers University of Technology
      </motion.h1>
      <motion.p className="education-text-2"
      key="/chalmers_text1"
      initial={{ opacity: 0, x:0, y:40 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0 }}>
  	  	The engineering program I studied at Chalmers was in Engineering Physics and included a BSc and a MSc.
      </motion.p>
      <motion.p className="education-text-1"
      key="/chalmers_text2"
      initial={{ opacity: 0, x:0, y: 103 + 2 * lineHeight}}
      animate={{ opacity: 1, x:0, y: 63 + 2 * lineHeight}}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.2 }}>
  	  	During the degrees, I learned a great deal about technical subjects concerning advanced mathematics and physics. However, most of all, I learned how to solve complex problems in different fields under intense time pressure.
      </motion.p>
      <motion.p className="education-text-1"
      key="/chalmers_text3"
      initial={{ opacity: 0, x:0, y: 123 + 6 * lineHeight }}
      animate={{ opacity: 1, x:0, y: 83 + 6 * lineHeight }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.3 }}>
        The program included two theses, one each for the BSc and the MSc. My bachelor's thesis was about creating autonomous robots that would avoid colliding with each other. Read about it <span style={{color:'#ff9933', fontWeight:'bold'}}>here</span>.
        My master's thesis was about developing adaptive collaborative robots in ABBs robot simulation software RobotStudio. You can read about it <span style={{color:'#ff9933', fontWeight:'bold'}}>here</span>.
      </motion.p>
      </div>
    case 2:
     return <div className="text-container">
     <motion.h1 className="education-text-h"
     key="/graz_text_h"
     initial={{ opacity: 0, x:0, y:20 }}
     animate={{ opacity: 1, x:0, y:0 }}
     exit={{ opacity: 0 }}
     transition={{ duration: 0.5, delay: 0 }}>
       Graz University of Technology
     </motion.h1>
     <motion.p className="education-text-2"
     key="/graz_text"
     initial={{ opacity: 0, x:0, y:40 }}
     animate={{ opacity: 1, x:0, y:0 }}
     exit={{ opacity: 0 }}
     transition={{ duration: 0.5, delay: 0 }}>
       During the first year of my Master of Science, I was able to take part of the <span style={{color:'#ff9933', fontWeight:'bold'}}>ERASMUS+ programme</span> and study a year abroad in Graz, Austria.
     </motion.p>
     <motion.p className="education-text-1"
     key="/graz_text2"
     initial={{ opacity: 0, x:0, y: 123 + 2 * lineHeight}}
     animate={{ opacity: 1, x:0, y: 83 + 2 * lineHeight}}
     exit={{ opacity: 0 }}
     transition={{ duration: 0.5, delay: 0.2 }}>
       I studied in Technische Universität Graz, where I took engineering courses related to my degree. Some of the courses were Adaptive Systems, Embedded Systems, and Optimization for Computer Science.
     </motion.p>
     <motion.p className="education-text-1"
     key="/graz_text3"
     initial={{ opacity: 0, x:0, y: 143 + 6 * lineHeight }}
     animate={{ opacity: 1, x:0, y: 103 + 6 * lineHeight }}
     exit={{ opacity: 0 }}
     transition={{ duration: 0.5, delay: 0.3 }}>
       Living and studying in a foreign country proved to be an invaluable experience both personally and professionally.
     </motion.p>
     </div>
    case 3:
      return <div className="text-container">
      <motion.h1 className="education-text-h"
      key="/nau_text_h"
      initial={{ opacity: 0, x:0, y:20 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0 }}>
  	  	Northern Arizona University
      </motion.h1>
      <motion.p className="education-text-2"
      key="/nau_text"
      initial={{ opacity: 0, x:0, y:50 }}
      animate={{ opacity: 1, x:0, y:0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0 }}>
  	  	After I completed my engineering degree, I decided to complement my technical skills with managerial and business knowledge.
        I therefore chose to pursue an MBA, and I wanted to do it in the country famous for its managerial university degrees.
      </motion.p>
      <motion.p className="education-text-1"
      key="/nau_text2"
      initial={{ opacity: 0, x:0, y: 123 + 3 * lineHeight}}
      animate={{ opacity: 1, x:0, y: 83 + 3 * lineHeight}}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.2 }}>
        I chose Northern Arizona University because of its repuation, location, and opportunities.
        It proved to be a good decision as I got to experience a beautiful city where I could expand my knowledge.
      </motion.p>
      <motion.p className="education-text-1"
      key="/nau_text3"
      initial={{ opacity: 0, x:0, y: 143 + 6 * lineHeight }}
      animate={{ opacity: 1, x:0, y: 103 + 6 * lineHeight }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5, delay: 0.3 }}>
        At NAU, I studied many courses related to business, management, leadership, accounting, and finance.
        I learned how to lead and develop people, how to use lean principles, and how to manage and analyze a company financially.
      </motion.p>
      </div>
    default:
      return null;
  };
}

function Education() {
  const [status, setStatus] = React.useState(0);
  const [custom, setCustom] = React.useState(1);
  const [[sizeBox1, sizeBox2, sizeBox3, sizeBox4], setSizeVar] = React.useState([1,1,1,1]);

  useEffect(() => {
    setCustom(1);
    setCustom(0);
  }, []); /*do this on load only once*/

  return (
    <div className="container-fluid">
      <div className="box-container">
        <motion.div className="box1_edu"
          key="/chalmers_pic"
          transition={{ duration: 0.5, delay: custom * 0.25 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#008000'}}
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox1, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 1) ? setStatus(1) : setStatus(0);
            (sizeBox1 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1.1,1,1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/education/chalmers_logo4.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box2_edu"
          key="/graz_pic"
          transition={{ duration: 0.5, delay: custom * 0.5 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#008000'}}
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox2, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 2) ? setStatus(2) : setStatus(0);
            (sizeBox2 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1,1.1,1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/education/graz_logo4.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box3_edu"
          key="/nau_pic"
          transition={{ duration: 0.5, delay: custom * 0.75 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#008000'}}
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox3, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 3) ? setStatus(3) : setStatus(0);
            (sizeBox3 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1,1,1.1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/education/nau_logo4.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box4_edu"
    		key='box4'
        transition={{ duration: 0.5, delay: custom * 1.0 }}
        whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#008000'}}
        whileTap={{ scale: 0.9 }}
        initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
        animate={{ opacity: 1, scale: sizeBox4, x:0, y: -10 }}
        exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
    		>

    		<Particles
    				params={{
    					particles: {
                number: {
                  value: 50,
                  density: {
                    enable: false,
                    value_area: 600
                  }
                },
                color: {
                  value: '#000000'
                },
                shape: {
            			/*
                  type: 'circle',
                  stroke: {
                    width: 0,
                    color: '#ffffff'
                  }*/
            			type: 'image',
            			image: {
            				src: '/general/leaf1.png',
            				width: 1,
            				height: 1
            			},
            			stroke: {
                    width: 0,
                    color: '#ffffff'
                  }
                },
                opacity: {
                  value: 0.2,
                  random: false,
                  anim: {
                    enable: false,
                    speed: 1,
                    opacity_min: 0.1,
                    sync: false
                  }
                },
                size: {
                  value: 10,
                  random: true,
                  anim: {
                    enable: false,
                    speed: 40,
                    size_min: 0.1,
                    sync: false
                  }
                },
                line_linked: {
                  enable: false,
                  distance: 150,
                  color: '#ffffff',
                  opacity: 0.4,
                  width: 1
                },
                move: {
                  enable: true,
                  speed: 5,
                  direction: 'left',
                  random: true,
                  straight: true,
                  out_mode: 'out',
                  bounce: false,
                  attract: {
                    enable: false,
                    rotateX: 600,
                    rotateY: 1200
                  }
                }
              },
              interactivity: {
                detect_on: 'canvas',
                events: {
                  onhover: {
                    enable: false,
                    mode: 'grab'
                  },
                  onclick: {
                    enable: true,
                    mode: 'repulse'
                  },
                  resize: true
                },
                modes: {
                  grab: {
                    distance: 200,
                    line_linked: {
                      opacity: 1
                    }
                  },
                  bubble: {
                    distance: 400,
                    size: 40,
                    duration: 2,
                    opacity: 8,
                    speed: 3
                  },
                  repulse: {
                    distance: 200,
                    duration: 0.4
                  },
                  push: {
                    particles_nb: 4
                  },
                  remove: {
                    particles_nb: 2
                  }
                }
              },
              retina_detect: true
            }}
    				style={{
    					height: '100%',
    					width: '100%'
    				}}
    			/>
    		</motion.div>
      </div>

      {ChoosePara(status)}
	  </div>
  );
}

export default Education;
