import React, {useEffect} from 'react';
import { motion } from "framer-motion";
import FadeIn from "react-lazyload-fadein";

function choosePara(status) {
        switch (status) {
          case 0:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/education_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	Profile
            </motion.h1>
            <motion.p className="education-text-1"
            key="/education_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
              Gained consulting experience managing two projects regarding the development of mobile applications using indoor tracking and holographic reality.
              <br /><br />
              International and multi-cultural experience from living and studying in Sweden, Austria, and the USA.
              <br /><br />
              Winner of Automation Student 2019 Award for best automation-related master thesis of the year. Fluent in Swedish, Persian, and English.
            </motion.p>
            </div>
          default:
            return null;
  };
}

function Profile() {
  const [status, setStatus] = React.useState(0);
  const [custom, setCustom] = React.useState(1);

  useEffect(() => {
    setCustom(1);
    setCustom(0);
  }, []); /*do this on load only once*/

  return (
    <div className="container-fluid">
      <div className="box-container">
        <FadeIn height={400} duration={300}>
        {onLoad =>
          <motion.img className="profile_pic"
          src="/home/website_arsam_picture_big.png" alt=""
      		key='profile_pic_key'
          transition={{ duration: 1.0, delay: custom * 0.5 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05}}
          whileTap={{ scale: 0.9 }}
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: 1, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onLoad={onLoad} />
        }
        </FadeIn>
      </div>

      {choosePara(status)}
	  </div>
  );
}

export default Profile;
