import React, {useEffect} from 'react';
import { motion } from "framer-motion";
import Particles from 'react-particles-js';
import FadeIn from "react-lazyload-fadein";

function choosePara(status) {
        switch (status) {
          case 0:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/education_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0}}>
        	  	Experience
            </motion.h1>
            <motion.p className="education-text-1"
            key="/education_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	After several years of advanced study, I have attained two graduate and one undergraduate degree. The degrees include an MBA from the W.A. Franke College of Business at Northern Arizona University, and a Master of Science in Engineering and a Bachelor of Science from Chalmers University of Technology.
              My MBA had a concentration on Applied Management, while my MSc was in System, Control and Mechatronics, and my BSc was in Engineering Physics.
            </motion.p>
            </div>
          case 1:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/chalmers_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	ABB
            </motion.h1>
            <motion.p className="education-text-1"
            key="/chalmers_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	After several years of advanced study, I have attained two graduate and one undergraduate degree. The degrees include an MBA from the W.A. Franke College of Business at Northern Arizona University, and a Master of Science in Engineering and a Bachelor of Science from Chalmers University of Technology.
              My MBA had a concentration on Applied Management, while my MSc was in System, Control and Mechatronics, and my BSc was in Engineering Physics.
            </motion.p>
            </div>
          case 2:
           return <div className="text-container">
           <motion.h1 className="education-text-h"
           key="/graz_text_h"
           initial={{ opacity: 0, x:0, y:20 }}
           animate={{ opacity: 1, x:0, y:0 }}
           exit={{ opacity: 0 }}
           transition={{ duration: 0.5, delay: 0 }}>
             Virtual Manufacturing
           </motion.h1>
           <motion.p className="education-text-1"
           key="/graz_text"
           initial={{ opacity: 0, x:0, y:50 }}
           animate={{ opacity: 1, x:0, y:0 }}
           exit={{ opacity: 0 }}
           transition={{ duration: 0.5, delay: 0 }}>
             After several years of advanced study, I have attained two graduate and one undergraduate degree. The degrees include an MBA from the W.A. Franke College of Business at Northern Arizona University, and a Master of Science in Engineering and a Bachelor of Science from Chalmers University of Technology.
             My MBA had a concentration on Applied Management, while my MSc was in System, Control and Mechatronics, and my BSc was in Engineering Physics.
           </motion.p>
           </div>
          case 3:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/nau_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	Chalmers International Reception Committee
            </motion.h1>
            <motion.p className="education-text-2"
            key="/nau_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	After several years of advanced study, I have attained two graduate and one undergraduate degree. The degrees include an MBA from the W.A. Franke College of Business at Northern Arizona University, and a Master of Science in Engineering and a Bachelor of Science from Chalmers University of Technology.
              My MBA had a concentration on Applied Management, while my MSc was in System, Control and Mechatronics, and my BSc was in Engineering Physics.
            </motion.p>
            </div>
          default:
            return null;
  };
}

function Experience() {
  const [status, setStatus] = React.useState(0);
  const [custom, setCustom] = React.useState(1);
  const [[sizeBox1, sizeBox2, sizeBox3, sizeBox4], setSizeVar] = React.useState([1,1,1,1]);

  useEffect(() => {
    setCustom(1);
    setCustom(0);
  }, []); /*do this on load only once*/

  return (
    <div className="container-fluid">
      <div className="box-container">
        <motion.div className="box1_exp"
          key="/chalmers_pic"
          transition={{ duration: 0.5, delay: custom * 0.25 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#e62e00'}} //scale can't be changed in whilehover and animate at the same time
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox1, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 1) ? setStatus(1) : setStatus(0);
            (sizeBox1 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1.1,1,1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/experience/website_ABB_logo3.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>
        <motion.div className="box2_exp"
          key="/graz_pic"
          transition={{ duration: 0.5, delay: custom * 0.5 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#e62e00'}} //this is stupid, for some reason scale can't be changed twice
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox2, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 2) ? setStatus(2) : setStatus(0);
            (sizeBox2 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1,1.1,1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/experience/website_virtual_logo.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box3_exp"
          key="/nau_pic"
          transition={{ duration: 0.5, delay: custom * 0.75 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#e62e00'}} //this is stupid, for some reason scale can't be changed twice
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox3, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 3) ? setStatus(3) : setStatus(0);
            (sizeBox3 === 1.1) ? setSizeVar([1,1,1,1]) : setSizeVar([1,1,1.1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/experience/website_circ_logo5.png" alt=""
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box4_exp"
    		key='box4'
        transition={{ duration: 0.5, delay: custom * 1.0 }}
        whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#e62e00'}} //this is stupid, for some reason scale can't be changed twice
        initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
        animate={{ opacity: 1, scale: sizeBox4, x:0, y: -10 }}
        exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
    		>

    		<Particles
    				params={{
    					particles: {
                number: {
                  value: 50,
                  density: {
                    enable: false,
                    value_area: 600
                  }
                },
                color: {
                  value: '#000000'
                },
                shape: {
            			/*
                  type: 'circle',
                  stroke: {
                    width: 0,
                    color: '#ffffff'
                  }*/
            			type: 'image',
            			image: {
            				src: '/general/leaf1.png',
            				width: 1,
            				height: 1
            			},
            			stroke: {
                    width: 0,
                    color: '#ffffff'
                  }
                },
                opacity: {
                  value: 0.2,
                  random: false,
                  anim: {
                    enable: false,
                    speed: 1,
                    opacity_min: 0.1,
                    sync: false
                  }
                },
                size: {
                  value: 10,
                  random: true,
                  anim: {
                    enable: false,
                    speed: 40,
                    size_min: 0.1,
                    sync: false
                  }
                },
                line_linked: {
                  enable: false,
                  distance: 150,
                  color: '#ffffff',
                  opacity: 0.4,
                  width: 1
                },
                move: {
                  enable: true,
                  speed: 5,
                  direction: 'left',
                  random: true,
                  straight: true,
                  out_mode: 'out',
                  bounce: false,
                  attract: {
                    enable: false,
                    rotateX: 600,
                    rotateY: 1200
                  }
                }
              },
              interactivity: {
                detect_on: 'canvas',
                events: {
                  onhover: {
                    enable: true,
                    mode: 'repulse'
                  },
                  onclick: {
                    enable: false,
                    mode: 'repulse'
                  },
                  resize: true
                },
                modes: {
                  grab: {
                    distance: 200,
                    line_linked: {
                      opacity: 1
                    }
                  },
                  bubble: {
                    distance: 400,
                    size: 40,
                    duration: 2,
                    opacity: 8,
                    speed: 3
                  },
                  repulse: {
                    distance: 50,
                    duration: 0.4
                  },
                  push: {
                    particles_nb: 4
                  },
                  remove: {
                    particles_nb: 2
                  }
                }
              },
              retina_detect: true
            }}
    				style={{
    					height: '100%',
    					width: '100%'
    				}}
    			/>
    		</motion.div>
      </div>

      {choosePara(status)}
	  </div>
  );
}

export default Experience;
